<template>
	<div class="thank-you">
		<div class="thank-you_inner">
			<Loader v-if="isLoad" />
			<template v-else>
				<div class="thank-you_image">
					<img :src="`${publicPath}images/like.svg`" alt="">
				</div>
				<div class="thank-you_title">
					<p>Спасибо, что выбрали наш центр!</p>
					<span>
						В ближайшее время наш специалист свяжется с Вами для подтверждения информации.
					</span>
				</div>
				<div class="thank-you_info">
					<p>
						По всем вопросам, связанным с приемом заявок в бюджетные объединения, обращайтесь в рабочее время к заместителю директора по учебно-воспитательной работе<br>
						тел. (821) 482-75-00
					</p>
				</div>
			</template>
		</div>
	</div>
</template>

<script>
	import Loader from './../UI/Loader'

	import pdfMake from 'pdfmake/build/pdfmake'
	import pdfFonts from 'pdfmake/build/vfs_fonts'

	var globalBase64Document = ''
	var globalFiles = ''

	export default {
		components: { Loader },
		data() {
			return {
				publicPath: process.env.BASE_URL,
				load: false,
				base64Document: '',
				files: ''
			}
		},
		methods: {
			buildPDF() {
				return new Promise( (resolve, reject) => {
					let base64Document

					pdfMake.vfs = pdfFonts.pdfMake.vfs

					let formData = this.$store.getters.getFormData
					let unions = this.$store.getters.getUnions.map( val => val.title)

					let docBody = [
						{
							style: 'header',
							stack: [
								{ text: 'Новая заявка' },
								{ text: `${ formData.fio.val } - ${ this.$store.getters.getAge } лет`, fontSize: 14, bold: false }
							]
						},
						{
							margin: [0, 0, 0, 20],
							stack: [
								{
									text: 'Направление:',
									style: 'subheader'
								},
								{
									text: this.directionName(this.$store.getters.getDirection)
								}
							],
						},
						{
							margin: [0, 0, 0, 20],
							stack: [
								{
									text: 'Форма обучения:',
									style: 'subheader'
								},
								{
									text: this.payName(this.$store.getters.getPayForm)
								}
							],
						},
						{
							margin: [0, 0, 0, 20],
							stack: [
								{
									text: 'Выбранные объединения:',
									style: 'subheader'
								},
								{
									ul: unions
								}
							]
						},
						{
							margin: [0, 0, 0, 20],
							stack: [
								{
									text: 'Данные кандидата:',
									style: 'subheader'
								},
								{
									ul: [
										`${ formData.fio.title }: ${ formData.fio.val }`,
										`${ formData.bornInfo.title }: ${ formData.bornInfo.val }`,
										`${ formData.bornDate.title }: ${ formData.bornDate.val }`,
										`${ formData.healthLimits.title }: ${ formData.healthLimits.val }`,
										`${ formData.citizenship.title }: ${ formData.citizenship.val }`,
										`${ formData.school.title }: ${ formData.school.val }`,
										`${ formData.regAddress.title }: ${ formData.regAddress.val }`,
										`${ formData.phone.title }: ${ formData.phone.val }`,
										`${ formData.liveAddress.title }: ${ formData.liveAddress.val }`,
										`${ formData.addressMatch.title }: ${ formData.addressMatch.val }`,
										`${ formData.parentFio.title }: ${ formData.parentFio.val }`,
										`${ formData.parentEmail.title }: ${ formData.parentEmail.val }`,
										`${ formData.parentPhone.title }: ${ formData.parentPhone.val }`,
										`${ formData.parentAddress.title }: ${ formData.parentAddress.val }`,
										`${ formData.parentAddressMatch.title }: ${ formData.parentAddressMatch.val }`,
										`${ formData.agree.title }: ${ formData.agree.val }`
									]
								}
							]
						},
						{
							margin: [0, 0, 0, 20],
							stack: [
								{
									text: 'Файлы заявки в приложении к Email',
									style: 'subheader'
								}
							]
						}
					]

					let docDefinition = {
						info: {
							title:'Новая заявка',
							author:'DiceMasters',
						},
						pageSize:'A4',
						pageOrientation:'landscape',
						pageMargins:[50,50,30,60],
						content: docBody,
						styles: {
							header: {
								fontSize: 22,
								bold: true,
								alignment: 'center',
								margin: [0, 30],
							},
							subheader: {
								fontSize: 16,
								bold: true,
								margin: [0, 0, 0, 10]
							}
						}
					}

					const pdfDocGenerator = pdfMake.createPdf(docDefinition);
					pdfDocGenerator.getBase64( data => {
						globalBase64Document = data
						globalFiles = formData.files.val

						resolve()
					})
				})
			},
			directionName(text) {
				let dir = ''

				switch (text) {
					case 'tech':
						dir = 'Техническая направленность'
						break
					case 'socped':
						dir = 'Социально-педагогическая направленность'
						break
					case 'esenscien':
						dir = 'Естественно-научная направленность'
						break
					case 'physsport':
						dir = 'Физкультурно-спортивная направленность'
						break
					default:
						dir = ''
				}

				return dir
			},
			payName(text) {
				let name = ''

				switch (text) {
					case 'free':
						name = 'Бесплатная форма обучения'
						break
					case 'pay':
						name = 'Платная форма обучения'
						break
					default:
						name = ''
				}

				return name
			},
			sendMail() {
				let host = 'https://warm-ocean-92766.herokuapp.com'

				const data = {
					pdf: globalBase64Document,
					files: globalFiles
				}

				this.axios.post(`${host}/gateway`, data)
					.catch( err => {
						console.log(err)
					})
					.finally( () => {
						this.load = true
					})
			}
		},
		computed: {
			isLoad() {
				return (!this.load) ? true : false
			}
		},
		mounted() {
			this.buildPDF().then( () => {
				this.sendMail()
			}).catch(err => {
				console.log(err)
			})
		}
	}
</script>

<style lang="less" scoped>
	.thank-you {
		width: 100%;
		height: 720px;
		font-family: 'Roboto', sans-serif;

		&_inner {
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
		}

		&_image {
			margin-bottom: 33px;

			img {
				width: auto;
				height: 120px;
			}
		}

		&_title {
			max-width: 470px;
			font-weight: 300;
			text-align: center;
			margin-bottom: 90px;
			position: relative;

			p {
				font-size: 30px;
				margin-bottom: 25px;
			}

			span { font-size: 14px; }

			&:after {
				content: '';
				max-width: 100%;
				width: 350px;
				height: 1px;
				background-color: #E2E2E2;
				position: absolute;
				bottom: -50px;
				left: 50%;
				transform: translateX(-50%);
			}
		}

		&_info {
			max-width: 600px;
			font-weight: 300;
			text-align: center;

			p { font-size: 14px; }
		}
	}
</style>